.case-type-count {
  font-size: 43px;
  color: #00f6ff;
  li {
    background: url("./images/count-bg.png") no-repeat;
    width: 425px;
    height: 157px;
    margin-bottom: 30px;
    text-align: center;
    &:nth-child(1) {
      background: url("./images/count-bg2.png") no-repeat;
      p.name {
        color: #ffc80b;
      }
      p.value i {
        background: url("./images/arrow2.png") no-repeat;
      }
    }
    p.value {
      color: #ffc80b;
      font-family: digifacewide;
      margin-top: 46px;
      span {
        font-size: 21px;
        float: right;
        padding-right: 15px;
        i {
          display: inline-block;
          background: url("./images/arrow.png") no-repeat;
          width: 16px;
          height: 23px;
          margin: 5px 0 0 5px;
        }
      }
    }
  }
}


