.time-nav {
  background: url(./images/nav-bg.png) no-repeat;
  width:1148px;
  height: 78px;
  li {
    height: 78px;
    padding: 0 15px;
    font-size: 48px;
    color: #fff;
    line-height: 78px;
    float: left;
    text-align: center;
    margin: 0 21px;
    cursor: pointer;
    &:last-child {
      margin-right:0;
      padding-right:0;
    }
    i {
      float: right;
      background: url(./images/arrow.png) no-repeat;
      width: 31px;
      height: 17px;
      margin: 32px 20px 0 8px;
    }
    &:hover {
      color: #06ffff;
    }
  }
  li.active {
    color: #06ffff;
  }
}


