.case-count-list {
  float: left;
  li {
    background: url("./images/count-bg.png") no-repeat left bottom ;
    width: 560px;
    height: 245px;
    font-size: 60px;
    color: #fff;
    margin-bottom: 74px;
    text-align: center;
    cursor: pointer;
    i {
      background: url("./images/icon.png") no-repeat;
      width: 34px;
      height: 36px;
      display: inline-block;
      margin-right: 15px;
      pointer-events: none;
    }
    p {
      width: 100%;
      font-family: digifacewide;
      font-size: 72px;
      color: #1cffff;
      margin-top: 55px;
      pointer-events: none;
    }
  }
}


