* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

body {
   background: url(./images/body-bg.jpg) no-repeat;
   background-size: 100% 100%;
   width: 2800px;
   height: 2100px;
   color: #fff;
   font-family: '微软雅黑';
}

.container {
  .pages-header {
    height: 355px;
    width: 2424px;
    position: absolute;
    top: 270px;
    left: 200px;
    z-index: 2;
    .dirt-count, .people-count {
      display: none;
    }
  } 
  .header-time-nav {
    position: absolute;
    left: 201px;
    top: 620px;
    z-index: 4;
  }
  .right-type-nav {
    position: absolute;
    right: 20px;
    top: 260px;
    z-index: 4;
  }
  .header-time-nav {
    position: absolute;
    left: 901px;
    top: 315px;
    z-index: 4;
  }

  /*地球*/
  .earth-wrap {
    position: absolute;
    z-index: 1;
    top: 400px;
    canvas {
      background-color:rgba(0, 0, 0, 0); 
    }
  }
  .case-count {
    position: absolute;
    z-index: 4;
    top: 415px;
    left: 50px;
  }
  .case-type-count {
    position: absolute;
    right: 30px;
    top: 430px;
  }
  .map-container {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0); 
    z-index: 2;
  }
}




